.people {
    justify-content: center;
}

.people_list {
    padding: 115px 0 100px;
    display: flex;
    flex-wrap: wrap;
}

.people_item {
    display: block;
    width: 270px;
    margin: auto;
    margin-bottom: 15px;
}

.people_img {
    width: 270px;
    height: 270px;
    overflow: hidden;
    margin-bottom: 30px;
}

h3 {
    @include h3;

}

.people_position {
    display: block;
    color: #FF4343;
    font-family: "Raleway";
    font-size: 18px;
    font-weight: 400;
    text-transform: uppercase;
    margin-bottom: 18px;
}

.people_social {
    margin-top: 38px;


}

.people_social_icon:hover {
    @include social_icon;
}

.people_description {
    color: #6C7279;
    font-family: "Raleway";
    font-size: 14px;
    font-weight: 400;
}