.location {
    background-color: #353C65;
}

.location_info {
    display: flex;
    padding: 71px 0;
    margin: auto;
    flex-wrap: wrap;
    justify-content: space-between;
}

.location_items {
    max-width: 370px;
    margin: 0 15px 15px 15px;
}

.location_headers {
    @include h5;
}

.location_social_icon:hover {
    @include social_icon;
}

.location_nav {
    display: flex;
    justify-content: space-between;
    margin: auto;
    padding-top: 49px;
}

.location_description {
    color: #FFF;
    font-family: "Raleway";
    text-align: center;
    font-size: 18px;
    font-weight: 400;
    text-transform: capitalize;
    margin-top: 29px;

}