.grids {
    width: 100%;
    display: grid;
    gap: 2px;
    grid-template-columns: repeat(4, 25%);
    grid-template-rows: repeat(2, 50%);
}

.grids_link {
    display: flex;
    position: relative;
    z-index: 1;
    &:hover .grid_header {
        display: flex;
    }
    &:active .grid_header {
        background-color: #B90C0C;
    }
}

.grids_img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.grid_header {

    @include grid_header;
    display: none;

    .grid_h4 {
        @include grid_h4;
    }

    .grid_h5 {
        @include grid_h5;
    }


}
