.easily_list {
    padding: 91px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.easily_item {
    display: block;
    justify-content: center;
    width: 370px;
    padding: 25px 0 15px;
    margin: auto;
    align-items: center;

}

.easily_img {
    display: flex;
    justify-content: center;

}

.easily_about_card {
    color: #2D3033;
    text-align: center;
    font-family: "Raleway";
    font-size: 24px;
    font-weight: 900;
    text-transform: uppercase;
    padding: 45px 0 20px 0;
}

.easily_description {
    width: 370px;
    color: #6C7279;
    text-align: center;
    font-family: "Raleway";
    font-size: 18px;
    font-weight: 400;
}